import React from 'react'

const Note = ({ note, toggleImportance, deleteNote }) => {
  const label = note.important
    ? 'make not important' : 'make important'

  return (
    <li>
      <div class="input-group">
        {note.content}
        <button class="btn btn-outline-secondary" type="button" onClick={toggleImportance}>{label}</button>
        <button class="btn btn-outline-secondary" type="button" onClick={deleteNote}>x</button>
      </div>
      {/* <button onClick={toggleImportance}>{label}</button>
      <button onClick={toggleImportance}>x</button> */}
    </li>
  )
}

export default Note